import RadianButton from './buttons/radians/RadianButton';  // Importa HomeButton con mayscula

const Home = () => {
  return (
    <div className="mathphare-banner">
      <div className="background-image"></div>
      <div className="content">
        <h1>MATHPHARE</h1>
        <p>Calculadora grfica</p>
        <div className="button-container">
          <GradeButton /> 
          <RadianButton /> 
        </div>
      </div>
    </div>
  );
};

export default Home;
